/* toast */
.zfb-toast {
  padding: .1rem !important;
  background-color: #777; }
.zfb-toast .zfb-icon {
    font-size: .42rem;
    line-height: 1.3; }
.zfb-toast .mint-toast-text {
    padding-top: 0 !important; }
/* alert comfirm */
.mint-msgbox-title {
  font-weight: normal; }
.mint-msgbox-content {
  padding: .1rem .2rem !important;
  border-bottom: 0 !important;
  background-repeat: no-repeat;
  background-size: 100% 1px;
  background-position: bottom left;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#ddd), color-stop(50%, #ddd), color-stop(50%, transparent));
  background-image: linear-gradient(360deg, #ddd, #ddd 50%, transparent 50%); }
.mint-msgbox-btns {
  height: .4rem;
  line-height: .4rem; }
.mint-msgbox-btn {
  font-size: .17rem; }
.v-modal {
  opacity: 0.7; }

