.zfb-loading {
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}
.zfb-loading-wrapper {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  z-index: 2001;
  color: #fff;
  border-radius: .05rem;
  background-color: #333;
}
.zfb-loading-wrapper .text {
    display: block;
    color: #fff;
    text-align: center;
    font-size: .16rem;
}
.zfb-loading-wrapper .snake {
